.list-container {
    display: flex;
    flex-direction: column-reverse;
    margin-top: 2rem;
    background-color: #ff8053;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    border-radius: 2rem;
    padding: 1rem;
    justify-content: center;
    align-items: center;

}

.list-text {
    color: #232323;
    font-weight: bold;
    font-size: larger;
}